h1 a.highlighter, h2 a.highlighter, h3 a.highlighter, h4 a.highlighter, h5 a.highlighter, h5 a.highlighter {
  text-decoration: none;
  border-bottom: 1px solid rgba(56, 116, 168, 0.3);
  box-shadow: 0 -4px 0 rgba(56, 116, 168, 0.3) inset;
  transition: border-bottom .25s, box-shadow .25s; }
  h1 a.highlighter:hover, h2 a.highlighter:hover, h3 a.highlighter:hover, h4 a.highlighter:hover, h5 a.highlighter:hover, h5 a.highlighter:hover {
    text-decoration: none;
    border-bottom: 1px solid rgba(56, 116, 168, 0.8);
    box-shadow: 0 -4px 0 rgba(56, 116, 168, 0.8) inset; }

p a.highlighter {
  text-decoration: none;
  border-bottom: 1px solid rgba(56, 116, 168, 0.3);
  box-shadow: 0 -2px 0 rgba(56, 116, 168, 0.3) inset;
  transition: border-bottom .25s, box-shadow .25s; }
  p a.highlighter:hover {
    text-decoration: none;
    border-bottom: 1px solid rgba(56, 116, 168, 0.8);
    box-shadow: 0 -2px 0 rgba(56, 116, 168, 0.8) inset; }

.container .footerclass {
  background: url(images/blue-ridge-footer.png) no-repeat center bottom; }

.sidebar-footer-logos {
  background-color: rgba(255, 255, 255, 0.4);
  padding-top: 1.25em;
  padding-bottom: 1.25em;
  border-top: 1px solid #dadada;
  border-bottom: 1px solid #dadada; }

.widget [class*="wp-image"] {
  margin-left: 0;
  margin-right: 0; }
